<template>
  <div class="member-box">
    <div class="query-wrapper">
      <div>
        <el-form :inline="true" :model="queryForm" ref="queryForm" label-width="100px">
          <el-form-item label="部门名称">
            <el-autocomplete
              class="w100"
              v-model="queryForm.deptName"
              value-key="deptName"
              :fetch-suggestions="(queryString, cb) => queryInputAsync(queryString, cb, 'customer')"
              :placeholder="'input' | solvePlaceholder"
              @select="(val) => queryInputAsync('', val,'customer', true)"
            />
          </el-form-item>
          <el-form-item label="状态">
            <el-select v-model="queryForm.status" :placeholder="'select' | solvePlaceholder" class="w100">
              <el-option value="0" label="启用"></el-option>
              <el-option value="1" label="禁用"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <div class="query-btn-group mt15">
        <el-button type="primary" @click="queryContent('query')">查询</el-button>
        <el-button @click="reset">重置</el-button>
        <el-button @click="()=>dialogFormVisible=true">新增</el-button>
      </div>
    </div>
    <el-table :data="departs">
      <el-table-column prop="userName" label="部门名称" />
      <el-table-column prop="userName1" label="负责人" />
      <el-table-column prop="num" label="状态" />
      <el-table-column prop="result" label="创建时间" />
      <el-table-column prop="result" label="操作" />
    </el-table>

    <el-dialog
      title="修改或添加"
      :visible.sync="dialogFormVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :before-close="cancelDepart('departForm')">
      <el-form :model="departForm" ref="departForm">
        <el-form-item label="上级部门" prop="leaderId">
          <el-input :placeholder="'input' | solvePlaceholder" />
          <el-select v-model="departForm.leaderId">
            <el-option v-for="(dItem,index) in upDeparts" :key="dItem | solveKey"  :label="dItem.name" :value="dItem.id"/>
          </el-select>
        </el-form-item>
        <el-form-item label="部门名称" prop="deptName">
          <el-input :placeholder="'input' | solvePlaceholder" v-model="departForm.deptName"/>
        </el-form-item>
        <el-form-item label="联系电话" prop="phone">
          <el-input :placeholder="'input' | solvePlaceholder" v-model="departForm.phone"/>
        </el-form-item>
        <el-form-item label="负责人" prop="leaderName">
          <el-input :placeholder="'input' | solvePlaceholder" v-model="departForm.leaderName"/>
        </el-form-item>
        <el-form-item label="部门状态" prop="status">
          <el-input :placeholder="'input' | solvePlaceholder" />
          <el-radio-group v-model="departForm.status">
            <el-radio :label="0">启用</el-radio>
            <el-radio :label="1">禁用</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>

      <span slot="footer">
        <el-button type="primary" @click="saveDepart('departForm')">保存</el-button>
        <el-button @click="cancelDepart('departForm')">取消</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  import {Apis} from '@/axios'
  import {Message} from 'element-ui'
  import MemberPagination from '@/components/memberPagination'
  import {treeToArray, removeDuplicateObj} from '@/utils/tools'

  export default {
    name: 'Departments',
    data() {
      return {
        queryForm: {
          deptName:'',
          status:''
        },
        queryFormCopy:{},
        departs: [],
        dialogFormVisible: false,
        departForm:{
          parentId: '',
          parentName: '',
          phone: '',
          status: '',
          deptName: '',
          leaderId: '',
          leaderName:''
        },
        upDeparts:[],
      }
    },
    components:{MemberPagination},
    mounted() {
      let _this = this
      _this.init()
    },
    methods: {
      init() {
        let _this = this
        _this.queryContent('query')
      },
      queryContent(type, _pages) {
        let _this = this
        let {deptPage} = Apis.member
        let params = type == 'query' ? {..._this.queryForm} : type == 'pagination' ? {..._this.queryFormCopy}: {}
        params = _pages ? {...params, ..._pages} : {...params, ..._this.pages}
        deptPage(params).then(res => {
          if (res.data.status == 200) {
            let {records, total} = res.data.data
            _this.total = total
            _this.departs = records
          } else _this.$message.error(res.data.msg)
        })
      },
      reset() {},
      queryInputAsync(keyWord, cb, type, isSelect) {
        let _this = this
        let {deptTree} = Apis.member
        if (isSelect) {
          let {id} = cb
          _this.followForm.id = id
        } else {
          deptTree().then(res => {
            if (res.data.status == 200) {
              let trees = treeToArray(res.data.data)
              let data = removeDuplicateObj(trees, 'id')
              cb(data)
            }
            else Message.error(res.data.msg)
          })
        }
      },
      saveDepart(formRef){

      },
      cancelDepart(formRef) {

      },
    }
  }
</script>
<style lang="less" scoped>
  .member-box {
    height: 100%;
    overflow-y: auto;
    box-sizing: border-box;
    .query-wrapper{
      margin-bottom: 30px;
    }
    .query-btn-group{
      text-align: right;
    }
  }
</style>
